* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background: #f1f5f8;
  color: #222;
}

.title {
  text-align: center;
  margin-top: 4rem;
  text-transform: capitalize;
}

.booklist {
  /* width: 90vw;
    max-width: 1170px;
    we can shoerten this code by doing this */
  width: min(1170px, 90vw);
  margin: 5rem auto;
  display: grid;
  gap: 2rem;
}

@media screen and (min-width: 768px) {
  .booklist {
    grid-template-columns: repeat(3, 1fr);
    align-items: start;
  }
}

.book {
  background-color: #fff;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  position: relative;
}

.book img {
  width: 100%;
  object-fit: cover;
}

.book h2 {
  margin-top: 1rem;
  font-size: 1rem;
}

.book h4 {
  font-size: 1rem;
  color: #617d98;
  margin-top: 0;
  letter-spacing: 2px;
}

.book p {
  margin: 1rem 0 0 0.5rem;
}

.number {
  position: absolute;
  top: 0;
  left: 0;
  padding: 0.75rem;
  background-color: #c35600;
  border-radius: 1rem 0 1rem 0;
  color: #fff;
}
